<template>
    <div style="">
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">设置</p>
        </div>
        <div class="msgBox">
            <div class="msgLine">
                <div class="msgTitle f30">用户协议</div>
                <img class="msgTo" src="../../assets/my/more.png" alt="">
            </div>
        </div>
        <div class="msgBox" @click="feedback">
            <div class="msgLine">
                <div class="msgTitle f30">意见反馈</div>
                <img class="msgTo" src="../../assets/my/more.png" alt="">
            </div>
        </div>
        <div class="msgBox">
            <div class="msgLine">
                <div class="msgTitle f30">版本</div>
                <div class="f26 c999 msgName">1.0.0</div>
            </div>
        </div>
        <div class="msgBox">
            <div class="msgLine">
                <div class="msgTitle f30">清除缓存</div>
                    <div class="f26 c999 msgName">125.6M</div>
            </div>
        </div>
        
        <div class="centerBtn f26 cfff text-center" @click="loginOut">退出登录</div>
    </div>
    
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            name: '杨会如',
            active: 2,
            icon: {
                home: require('../../assets/my/home.png'),
                hmeActive: require('../../assets/my/homeActive.png'),
                msg:require('../../assets/my/msg.png'),
                msgActive:require('../../assets/my/msgActive.png'),
                my:require('../../assets/my/my.png'),
                myActive:require('../../assets/my/myActive.png')
            }
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        loginOut(){
            localStorage.removeItem('zhxy_user');
            this.$router.push({path:'/login'})
        },
        feedback(){
           this.$router.push({path:'/feedback'}) 
        }
    }
}
</script>

<style scoped>
    .msgBox{width: 100%;overflow: hidden;}
    .msgLine{margin-left: 0.3rem;border-bottom: 1px solid #f2f2f2;overflow: hidden;height: 1rem;line-height: 1rem;}
    .msgTitle{float: left;}
    .msgHead{width: 0.7rem;height: 0.7rem;display: block;float:right;border-radius: 50%;margin: 0.15rem 0.2rem 0 0;}
    .msgTo{width: 0.3rem;height: 0.3rem;float: right;margin: 0.35rem 0.3rem 0 0;}
    .msgName{float: right;margin-right: 0.3rem;}
    .Introduction{width: 100%;box-sizing: border-box;padding:0 0.3rem;height: 3.65rem;border-bottom: 1px solid #f2f2f2;}
    .IntroductionTitle{line-height: 1rem;}
    .content{clear: both;}
    .photo{width: 100%;box-sizing: border-box;padding:0 0.3rem;}
    .phoneTitle{line-height: 1rem;}
    .phoneTitle p{display: inline-block;}
    .phoneBox{clear: both;overflow: hidden;}
    .imgShow{width: 1.2rem;height: 1.2rem;border-radius: 5px;overflow: hidden;float: left;}
    .imgShow img{width: 100%;height: 100%;display: block;}
    .imgShow:nth-child(5n+1){margin-left: 0;}
    .centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: absolute;bottom: 0.6rem;left: 50%;margin-left: -3.45rem; }
</style>


